export default {
	data() {
		return {
			activeSteps: 1, // 当前步骤
			disabledOne: false,
			disabledTwo: false,
			disabledThree: false,
			stepsOne: {
				shopName: "", // 店铺名称
				businessName: "", // 商家全称
				legalPerson: "", // 法人姓名
			},
			imgUrl: "",
			tempFilePaths: [],
			BusinessLicense: "", // 营业执照
			stepsThree: {
				userName: "", // 真实姓名
				IDCode: "", // 身份证号码
				phone: "", // 手机号码
				code: "", // 验证码
			},
			// 验证码
			safety: {
				time: 60,
				state: false,
				interval: ''
			},
			phoneReg: /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/,
		}
	},
	onLoad(options) {
		if (options.steps) this.activeSteps = options.steps;

	},
	watch: {
		// 监听第一步
		stepsOne: {
			handler(newValue) {
				if (newValue.shopName && newValue.businessName && newValue.legalPerson) {
					this.disabledOne = true;
				} else {
					this.disabledOne = false;
				}
			},
			deep: true
		},
		BusinessLicense: {
			handler(newValue) {
				this.disabledTwo = !!newValue
			}
		},
		// 监听第三步
		stepsThree: {
			handler(newValue) {
				if (newValue.userName && newValue.IDCode && this.phoneReg.test(newValue.phone) && newValue.code
					.length === 6) {
					this.disabledThree = true;
				} else {
					this.disabledThree = false;
				}
			},
			deep: true
		},
	},
	methods: {
		navBack() {
			uni.navigateBack({
				delta: 1
			})
		},
		nextSteps() {
			this.activeSteps++;
		},
		chooseImage() {
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success: res => {
					const tempFilePaths = res.tempFilePaths[0];
					this.tempFilePaths = res.tempFilePaths;
					this.BusinessLicense = this.imgUrl = tempFilePaths;
				}
			});
		},
		// 删除图片
		delImage(e) {
			uni.showModal({
				title: '删除照片',
				content: '确定要删除这张照片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.BusinessLicense = this.imgUrl = ""
						this.tempFilePaths = []
					}
				}
			})
		},
		// 预览
		previewImage(e) {
			uni.previewImage({
				urls: this.tempFilePaths,
				current: e.currentTarget.dataset.src
			})
		},
		// 获取验证码
		fnGetPhoneCode() {
			if (this.phoneReg.test(this.stepsThree.phone)) {
				uni.showToast({
					title: "正在发送验证码",
					icon: "loading",
					success: () => {
						// 成功后显示倒计时60s后可在点击
						this.safety.state = true;
						// 倒计时
						this.safety.interval = setInterval(() => {
							if (this.safety.time-- <= 0) {
								this.safety.time = 60;
								this.safety.state = false;
								clearInterval(this.safety.interval);
							}
						}, 1000);
						uni.showToast({
							title: "发送成功",
							icon: "success"
						})
					}
				})
			} else {
				uni.showToast({
					title: "手机号不正确",
					icon: "none"
				})
			}
		},
		submit() {
			uni.navigateTo({
				url: "/pages/login/shopAuth"
			})
		},
	}
}
